<script lang="ts">
	import { onMount } from "svelte";
	import type Toaster from "./Toaster.svelte";
	import Setting from "./Setting.svelte";

	export let state: GameState;
	export let gameSetup: GameSetup;
	export let toaster: Toaster;

	let root: HTMLElement;
	onMount(() => {
		root = document.documentElement;
	});
	$: {
		if (root) {
			gameSetup.darktheme ? root.classList.remove("light") : root.classList.add("light");
			gameSetup.colorblind ? root.classList.add("colorblind")	: root.classList.remove("colorblind");
			localStorage.setItem('settings', JSON.stringify(gameSetup));
		}
	}
</script>

<!-- not currently supported, see https://github.com/sveltejs/svelte/issues/3105 -->
<!-- <svelte:body class:light={!$settings.darktheme} class:colorblind={$settings.colorblind} /> -->
<div class="outer">
	<div class="settings-top">
		<h3>settings</h3>
		<div
			on:click={() => {
				if (!state.validHard) {
					toaster.pop("You can't change mode during the game.");
				}
			}}
		>
			<Setting bind:value={gameSetup.hardmode} disabled={!state.validHard}>
				<span slot="title">Hard Mode</span>
				<span slot="desc">Any revealed hints must be used in subsequent tried</span>
			</Setting>
		</div>
		<Setting bind:value={gameSetup.darktheme}>
			<span slot="title">Dark Theme</span>
		</Setting>
		<Setting bind:value={gameSetup.colorblind}>
			<span slot="title">Color Blind Mode</span>
			<span slot="desc">High contrast colors</span>
		</Setting>
	</div>
</div>

<style>
	.outer {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	:global(.settings-top > div) {
		padding: 16px 0;
		border-bottom: 1px solid var(--border-primary);
	}
</style>
