<script lang="ts">

	import GameSlot from "./GameSlot.svelte";
	import ContextMenu from "./ContextMenu.svelte";
	import { createEventDispatcher } from "svelte";

	export let gameState: GameState;

	export function shake(row: number) {
		slots[row].shake();
	}
	export function bounce(row: number) {
		slots[row].bounce();
	}
	export function hideCtx(e?: MouseEvent) {
		if (!e || !e.defaultPrevented) showCtx = false;
	}
	const dispatch = createEventDispatcher();

	let slots: GameSlot[] = [];
	let showCtx = false;
	let x = 0;
	let y = 0;
	let word = "";

	function context(cx: number, cy: number, num: number, val: string) {
		if (gameState.tried >= num) {
			x = cx;
			y = cy;
			word = gameState.tried > num ? val : "";
			showCtx = word.length > 0 ? true : false;
		}
	}
</script>

{#if showCtx}
	<ContextMenu {x} {y} {word} />
{/if}

<div class="board">
	{#each gameState.guesses as guess, i}
		<GameSlot
			bind:this={slots[i]}
			num={i}
			{gameState}
			on:ctx={(e) => context(e.detail.x, e.detail.y, i, guess)}
		/>
	{/each}
</div>

<style>
	.board {
		display: grid;
		grid-template-rows: repeat(var(--rows), minmax(54px, auto));
		gap: 5.5px;
		max-width: var(--game-width);
		max-height: 420px;
		flex-grow: 1;
		aspect-ratio: var(--cols) / var(--rows);
		padding: 10px;
		position: relative;
	}
</style>
