<script lang="ts">
	import {
		initStatistics,
		createNewGame,
		initGameSettings,
		initKeyStates,
		ROWS,
        newSeed,
        COLS,
	} from "./utils";
	import Worzle from "./components/Worzle.svelte";
	import { keyStates } from "./stores";
	import { setContext } from "svelte";

	export let version: string;
	setContext("version", version);
	localStorage.setItem("version", version);

	let gameHisto: GameHisto;
	let gameState: GameState;
	let gameSetup: GameSetup;

	gameSetup = (JSON.parse(localStorage.getItem('settings')) as GameSetup) || initGameSettings();
	gameHisto = (JSON.parse(localStorage.getItem(`statistics`)) as GameHisto) || initStatistics();
	gameState = (JSON.parse(localStorage.getItem(`gamedata`)) as GameState) || createNewGame(newSeed());

	// Set the letter states when data for a new game mode is loaded so the keyboard is correct
	const letters = initKeyStates();
	for (let row = 0; row < ROWS; row++) {
		for (let col = 0; col < COLS; col++) {
			if (letters[gameState.guesses[row][col]] === "🔳" || gameState.keyStates[row][col] === "🟩") {
				letters[gameState.guesses[row][col]] = gameState.keyStates[row][col];
			}
		}
	}
	keyStates.set(letters);

	$: {
		localStorage.setItem(`gamedata`, JSON.stringify(gameState));
		localStorage.setItem(`settings`, JSON.stringify(gameSetup));
	}
	
	document.title = "worzle | A word guessing game";
</script>

<Worzle bind:gameState={gameState} {gameHisto} {gameSetup} />
