<script lang="ts">
	import Header from "./Header.svelte";
	import Board from "./Board.svelte";
	import Keyboard from "./Keyboard.svelte";
	import Modal from "./Modal.svelte";
	import Toaster from "./Toaster.svelte";
	import Settings from "./Settings.svelte";
	import Tutorial from "./Tutorial.svelte";
	import Statistics from "./Statistics.svelte";
	import Distribution from "./Distribution.svelte";
	import Seperator from "./Seperator.svelte";
	import Definition from "./Definition.svelte";
	import ShareGame from "./ShareGame.svelte";
	import Share from "./Share.svelte";
	import Reload from "./Reload.svelte";
	import { fade } from "svelte/transition";
	import { getContext, onMount, setContext } from "svelte";
	
	import {
		contractNum,
		DELAY_INCREMENT,
		PRAISE,
		checkState,
		checkHardMode,
		ROWS,
		COLS,
		createNewGame,
		initKeyStates,
        newSeed,
	} from "../utils";
	import { keyStates } from "../stores";
    import { valid } from "../words_5";

	export let gameHisto: GameHisto;
	export let gameState: GameState;
	export let gameSetup: GameSetup;
	
	let toaster = getContext<Toaster>("toaster");

	setContext("toaster", toaster);
	const version = getContext<string>("version");

	// implement transition delay on keys
	const delay = 2000;

	let showTutorial = gameSetup.tutorial; 
	let showSettings = false;
	let showStats = false;

	let board: Board;

	function submitWord() {
		if (gameState.guesses[gameState.tried].length !== COLS) {
			toaster.pop("Not enough letters");
			return board.shake(gameState.tried);
		} 
		
		if (!valid.includes (gameState.guesses[gameState.tried])) {
			toaster.pop("Not in word list");
			return board.shake(gameState.tried);
		} 

		if (gameState.tried > 0) {
			const hm = checkHardMode(gameState.guesses, gameState.keyStates, gameState.tried);
			if (gameSetup.hardmode) {
				if (hm.type === "🟩") {
					toaster.pop(`${contractNum(hm.pos + 1)} letter must be ${hm.char.toUpperCase()}`);
					board.shake(gameState.tried);
					return;
				} else if (hm.type === "🟨") {
					toaster.pop(`Guess must contain ${hm.char.toUpperCase()}`);
					board.shake(gameState.tried);
					return;
				}
			} else if (hm.type !== "⬛") {
				gameState.validHard = false;
			}
		}

		const state = checkState(gameState.answer, gameState.guesses[gameState.tried]);
		state.forEach((e, i) => {
			const ls = $keyStates[gameState.guesses[gameState.tried][i]];
			if (ls === "🔳" || e === "🟩") {
				$keyStates[gameState.guesses[gameState.tried][i]] = e;
			}
		});
		gameState.keyStates[gameState.tried] = state;
		gameState.tried++;
		if (gameState.guesses[gameState.tried - 1] === gameState.answer) win();
		else if (gameState.tried === ROWS) lose();
	}

	function win() {
		board.bounce(gameState.tried - 1);
		gameState.active = false;
		setTimeout(
			() => toaster.pop(PRAISE[gameState.tried - 1]),
			DELAY_INCREMENT * (COLS + 1)
		);
		setTimeout(setShowStatsTrue, delay * 1.4);
		gameHisto.distrib[gameState.tried] += 1;
		gameHisto.played += 1;
		if ("streak" in gameHisto) {
			gameHisto.streak = gameHisto.streak + 1;
			if (gameHisto.streak > gameHisto.maxStreak) gameHisto.maxStreak = gameHisto.streak;
		}
		gameState.nextGame = newSeed();
		localStorage.setItem(`statistics`, JSON.stringify(gameHisto));
	}

	function lose() {
		gameState.active = false;
		setTimeout(setShowStatsTrue, delay);
		gameHisto.distrib.X += 1;
		gameHisto.played += 1;
		if ("streak" in gameHisto) gameHisto.streak = 0;
		gameState.nextGame = newSeed();
		localStorage.setItem(`statistics`, JSON.stringify(gameHisto));
	}

	function concede() {
		showSettings = false;
		setTimeout(setShowStatsTrue, DELAY_INCREMENT);
		lose();
	}

	function reload(idx: number = 0) {
		if (!idx) idx = newSeed();
		gameState = createNewGame(idx);
		
		console.log("Curent : ", idx);
		
		$keyStates = initKeyStates();
		showStats = false;
	}

	function setShowStatsTrue() {
		if (!gameState.active) showStats = true;
	}

	onMount(() => {
		if (!gameState.active) setTimeout(setShowStatsTrue, delay);
	});

	// $: gameState.active = !(showTutorial || showStats || showSettings);

</script>

<svelte:body on:click={board.hideCtx} />

<main class:tried={gameState.tried > 0} style="--rows: {ROWS}; --cols: {COLS}">
	<Header
		on:tutorial={() => (showTutorial = true)}
		on:stats={() => (showStats = true)}
		on:settings={() => (showSettings = true)}
	/>
	<Board bind:this={board} {gameState} />
	<Keyboard
		on:submitWord={submitWord}
		on:esc={() => {
			showTutorial = false;
			showStats = false;
			showSettings = false;
		}}
		disabled={!gameState.active || showSettings || showStats || showTutorial}
		bind:value={gameState.guesses[gameState.tried === ROWS ? 0 : gameState.tried]}
	/>
</main>

<Toaster bind:this={toaster} />

<Modal bind:visible={showTutorial}
	on:close|once={() => { 
		gameSetup.tutorial = false;
		console.log(showTutorial);
	}}
>
	<Tutorial />
</Modal>

<Modal bind:visible={showStats}>
	<Statistics data={gameHisto} />
	<Distribution gameScore={gameHisto.distrib} {gameState} />

	{#if !gameState.active}
		<Seperator visible={!gameState.active}>
			<Reload slot="1" 
				on:reload={() => reload(gameState.nextGame)}
				bind:gameID={gameState.nextGame} 
			/>
			<Share slot="2" state={gameState} {toaster} />
		</Seperator>

		<Definition word={gameState.answer} alternates={2} />
	{:else}
		<ShareGame gameID={gameState.gameSeed} {toaster} />
		<Seperator visible={gameState.active}>
			<div slot="1" in:fade={{ delay: 300 }} class="concede" on:click={concede}>give up</div>
			<div slot="2">
				<a href="https://www.nytimes.com/games/wordle/" target="_blank">Original Wordle</a>
				<div>
					<div>Worzle Ver.{version}</div>
					<div
						title="Double click to reset your statistics"
						on:dblclick={() => {
							localStorage.clear();
							toaster.pop("localStorage cleared");
						}}
					>
						Clear & Reset
					</div>
				</div>
			</div>
		</Seperator>
	
	{/if}
</Modal>

<Modal bind:visible={showSettings}>
	<Settings state={gameState} {gameSetup} {toaster} />
</Modal>

<style lang="scss">
	main {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		height: 100%;
		max-width: var(--game-width);
		margin: auto;
		position: relative;
	}
	.concede {
		margin-top: 15px;
		text-transform: uppercase;
		color: #fff;
		cursor: pointer;
		font-size: var(--fs-medium);
		font-weight: bold;
		padding: 15px;
		border-radius: 4px;
		text-align: center;
		background-color: var(--red);
		&:hover {
			opacity: 0.9;
		}
	}
</style>
