<script lang="ts">
	import { createEventDispatcher } from "svelte";
	import { COLS } from "../utils";
	import Tile from "./Tile.svelte";

	export let num: number;
	export let gameState: GameState;

	export function shake() {
		animation = "shake";
	}
	export function bounce() {
		tiles.forEach((e) => e.bounce());
	}
	const dispatch = createEventDispatcher();
	let animation = "";
	let tiles: Tile[] = [];
	
</script>

<div
	class="gameslot"
	on:dblclick|preventDefault={(e) => dispatch("ctx", { x: e.clientX, y: e.clientY })}
	on:animationend={() => (animation = "")}
	data-animation={animation}
	class:complete={gameState.tried > num}
>
	{#each Array(COLS) as _, i}
		<Tile  
			idx={i} 
			bind:this={tiles[i]} 
			state={gameState.keyStates[num][i]} 
			value={gameState.guesses[num].charAt(i)} 
		/>
	{/each}
</div>

<style lang="scss">
	.gameslot {
		display: grid;
		grid-template-columns: repeat(var(--cols), minmax(54px, auto));
		gap: 5px;
		&[data-animation="shake"] {
			animation: shake 0.6s;
		}
	}
	@keyframes shake {
		10%,
		90% {
			transform: translateX(-1px);
		}

		20%,
		80% {
			transform: translateX(2px);
		}

		30%,
		50%,
		70% {
			transform: translateX(-4px);
		}

		40%,
		60% {
			transform: translateX(4px);
		}
	}
</style>
